
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
  }
  
  body {
    margin: 0;
    background: linear-gradient(150deg, #e0ddddc4, #697565);
    min-height: 100vh;
  }
  
  .container {
    margin: 1rem;
    text-align: center;
  }
  
  .nav {
    background-color: #697565;
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
    padding: 0 1rem;
    min-height: 10vh;
  }
  
  .span {
    font-size: 2rem;
  }
  
  .site-title {
    font-size: 1.5rem;
    transition: transform 0.5s ease-in-out;
  }

  .site-title:hover {
    transform: scale(1.40);
  }

  .nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
  }
  
  .nav a {
    color: inherit;
    text-decoration: none;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
    cursor: pointer;
  }
  

  .nav li {
    position: relative;
    overflow: hidden;
  }
  
  .nav li::after {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    height: 3px; 
    width: 100%;
    background: linear-gradient(45deg, #140303 , #fff); 
    transform: translateX(-50%) scaleX(0);
    transition: transform 0.3s ease;
  }
  
  .nav li:hover::after {
    transform: translateX(-50%) scaleX(1);
  }
  
 